import React from 'react';

class Section extends React.Component {
  render() {
    return (
      <div id={this.props.id} className="section">
        <div className="section-container">
          {this.props.name ? <h1>{this.props.name}</h1> : undefined}
          {this.props.children}
        </div>
      </div>
    );
  }
}

export default Section;
